import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Menu } from "antd";

const items = [
  {
    label: "Home",
    key: "/",
  },
  {
    label: "Research",
    key: "/direction",
    children: [
      {
        label: "AI & Imaging Technologies",
        key: "/direction",
      },
      {
        label: "Scientific Applications",
        key: "/application",
      },
    ],
  },
  {
    label: "Publication",
    key: "/publication",
  },
  {
    label: "Team",
    key: "/team",
  },
  {
    label: "Teaching",
    key: "/teaching",
  },
  {
    label: "Contact",
    key: "/contact",
  },
];

const App = () => {
  const [current, setCurrent] = useState(""); // 
  const [openKeys, setOpenKeys] = useState([]); // 
  const navigate = useNavigate();

  const handleMenuSelect = ({ key }) => {
    setCurrent(key); // 
    navigate(key); // 
  };

  const handleMenuOpenChange = (keys) => {
    // 
    setOpenKeys(keys);
  };

  return (
    <div
      style={{
        padding: "10px 30px",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between", // 
      }}
    >
      <div style={{ display: "flex", alignItems: "center" }}>
        <img
          src="/rawdata/logo_temp.png"
          alt="Logo"
          className="h-10 sm:h-10 md:h-20 lg:h-23 xl:h-26 object-contain mr-2"
        />
      </div>

      {/*  Menu */}
      <Menu
        onClick={handleMenuSelect} // 
        selectedKeys={[current]} // 
        openKeys={openKeys} // 
        onOpenChange={handleMenuOpenChange} // /
        mode="horizontal"
        items={items.map((item) => ({
          ...item,
          onTitleClick: () => {
            // key
            if (!item.children) {
              navigate(item.key);
              setCurrent(item.key);
            } else if (item.key === "/direction") {
              navigate("/direction");
              setCurrent(item.key); //  Research 
            }
          },
        }))}
        style={{
          display: "inline-block", // 
          borderBottom: "none", // 
        }}
        className="text-xl"
      />
      <style>
        {`
          .ant-menu-horizontal > .ant-menu-item-selected::after {
            border-bottom: 2px solid #1890ff; /*  */
            transform: scaleX(1); /*  */
          }
          .ant-menu-horizontal > .ant-menu-item {
            margin: 0 8px; /*  */
          }
          /*  */
          .ant-menu-horizontal .ant-menu-item-selected {
            color: #1890ff !important; /*  */
          }
          .ant-menu-horizontal .ant-menu-item {
            transition: color 0.3s ease, transform 0.3s ease; /*  */
          }
        `}
      </style>
    </div>
  );
};

export default App;
